export const getCorrectDate = (inputDate: string) => {
  const date = new Date(inputDate);
  const month = date.getMonth();
  const currentMonth = new Date().getMonth();
  let weekDay = "";

  if (month !== currentMonth) return "  ";

  const day = date.getDate();
  const currentDay = new Date().getDate();
  const difference = currentDay - day;

  if (difference === 0) weekDay = "";
  else if (difference === 1) weekDay = "";
  else if (difference === 2) weekDay = "";
  else if (difference >= 3 && difference < 7) weekDay = "  ";
  else return "  ";

  const hours = date.getHours();
  const minutes = (date.getMinutes() < 10 ? "0" : "") + date.getMinutes();

  return `${weekDay}, ${hours}:${minutes} i-GMT+3`;
};
